.icon {
    --icon-size: 1.5rem;
    display: block;
    font-size: var(--icon-size);
    height: var(--icon-size);
    aspect-ratio: 1 / 1;
    text-align: center;
  }
  
  .icon-sm {
    --icon-size: 1rem;
  }
  
  .icon-lg {
    --icon-size: 2rem;
  }
  
  .icon-wrapper {
    --wrapper-padding: 1rem;
    --wrapper-bg: #ffecec;
    display: inline-block;
    color: var(--primary-color);
    padding: var(--wrapper-padding);
    border-radius: var(--rounded-full);
    background-color: var(--wrapper-bg);
  }
  
  .icon-wrapper-sm {
    --wrapper-padding: 0.5rem;
  }

  .icon-wrapper-sm > .icon {
    --icon-size: 1rem;
  }

  .icon-check {
    color: #2fab73;
  }

  .icon-star {
    color: #fea250;
  }

  .media-icon {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }

  .media-icon .icon-wrapper {
    --wrapper-bg: var(--bg-color);
    box-shadow: 0px 13px 18px rgba(17, 19, 35, 0.08);
  }

  .media-icon-sm > .icon-wrapper {
    --wrapper-padding: 0.5rem;
  }

  .media-icon-sm > .icon-wrapper > .icon {
    --icon-size: 1rem;
  }